let search = async (value) => {
  try {
    let result = await fetch(`https://api.github.com/users/${value}/repos`);
    if (result.status === 404) {
      alert("User not found");
      return;
    }
    let jsonResult = await result.json();
    return jsonResult;
  } catch (err) {
    alert(`Cannot fetch: ${err}`);
  }
};

export default search;
