import React, { useState } from "react";
import tw from "twin.macro";
import "tailwindcss/dist/base.min.css";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
const NavBar = ({ getRepos }) => {
  let [value, setValue] = useState("");
  let handleSubmit = (event) => {
    event.preventDefault();
    getRepos(value);
    setValue("");
  };

  let handleChange = (event) => {
    setValue(event.target.value);
  };
  return (
    <nav
      tw="py-2 flex lg:justify-end mb-2 shadow justify-center items-center"
      style={{
        lineHeight: "initial",
        backgroundColor: "initial",
        height: "initial",
        fontFamily: "Roboto",
      }}
    >
      <div tw="text-white w-full">
        <form tw="flex" onSubmit={handleSubmit}>
          <button tw="px-4 py-2 mr-5 bg-gray-700 rounded text-lg">
            <FontAwesomeIcon icon={faGithub} />
            <span tw="inline-block ml-2">Get Repos</span>
          </button>
          <input
            type="text"
            value={value}
            onChange={handleChange}
            tw="text-gray-800"
            placeholder="Enter your Github username here"
          />
        </form>
      </div>
    </nav>
  );
};

export default NavBar;
