const ErrorDisplay = ({ error }) => {
  console.log(error);
  let message = `Error on line ${error.loc.start.line}, column ${error.loc.start.column}`;

  return (
    <div tw="text-red-400 text-lg">
      <p>{message}</p>
      <pre>{error.codeFrame}</pre>
    </div>
  );
};

const Resumes = [Resume, Resume2];

function App() {
  let [data, setData] = useState(sample);
  let [stringData, setStringData] = useState(
    prettier.format(JSON.stringify(data), {
      parser: "json",
      plugins: [babylon],
    })
  );

  let [currentResume, setCurrentResume] = useState(0);
  let [isValid, setValid] = useState(true);

  useEffect(() => {
    window.localStorage.setItem("editState", stringData);
  }, [stringData]);
  let [error, setError] = useState({ status: false, error: {} });
  let onChange = (value) => {
    setStringData(value);
    try {
      prettier.format(value, {
        parser: "json",
        plugins: [babylon],
      });
      let newValue = JSON.parse(value);
      setData(newValue);
      setError({ status: false, error: {} });
      setValid(true);
    } catch (err) {
      setValid(false);
      if (!err.loc) {
        console.warn(err);
        return;
      }
      setError({
        status: true,
        error: err,
      });
    }
  };

  let handleGithubSearch = async (value) => {
    try {
      let res = await search(value);
      if (res) {
        console.log(res);
        let projects = res.slice(0, 5).map((project) => {
          return {
            project_name: project.name,
            link: project.html_url,
            description: [project.description],
            tagline: "",
            technology_used: { tools: [] },
          };
        });
        console.log(projects);
        let newValue = { ...data, github_projects: projects };
        onChange(JSON.stringify(newValue, null, 2));
      }
    } catch {}
  };

  let handleDownload = async () => {
    alert("Generating your pdf.... Please hold on");

    let response = await download({ resume: currentResume, data });
    if (response?.filename) {
      window.document.location.href = `/assets/${response.filename}`;
    } else {
      alert("Error occured generating your pdf");
    }
  };

  let handleReset = () => {
    onChange(JSON.stringify(sample, null, 2));
  };

  let handleClear = () => {
    onChange(JSON.stringify({}, null, 2));
  };

  let handleNextResume = () => {
    console.log("next");
    setCurrentResume(currentResume + 1);
  };

  let handlePrevResume = () => {
    console.log("prev");
    setCurrentResume(currentResume - 1);
  };

  let DisplayedResume = Resumes[currentResume];
  return (
    <>
      <NavBar getRepos={handleGithubSearch} />
      <div tw="flex flex-col lg:flex-row justify-between">
        <div tw="w-full mb-3 lg:mb-0 lg:w-5/12 relative">
          <div className="fixed-action-btn absolute top-0" tw="absolute top-0">
            <button className="btn-floating btn-large blue pulse">
              <i className="large material-icons">mode_edit</i>
            </button>
            <ul>
              <li>
                <button
                  className="btn tooltipped btn-floating yellow darken-1"
                  data-position="left"
                  data-tooltip="Reset"
                  id="reset"
                  onClick={handleReset}
                >
                  <i className="material-icons">refresh</i>
                </button>
              </li>

              <li>
                <button
                  className="btn tooltipped btn-floating blue"
                  data-position="left"
                  data-tooltip="Clear"
                  id="clear"
                  onClick={handleClear}
                >
                  <i className="material-icons">remove_circle_outline</i>
                </button>
              </li>
            </ul>
          </div>
          <AceEditor
            mode="json"
            theme="monokai"
            value={stringData}
            onChange={onChange}
            name="editor"
            editorProps={{ $blockScrolling: true }}
            height="80vh"
            width="100%"
            wrapEnabled
          />
        </div>
        <div tw="w-full lg:w-7/12 flex items-center" className="w-full">
          {error.status ? (
            <ErrorDisplay error={error.error} />
          ) : (
            <div
              tw="max-w-2xl my-auto mx-auto flex flex-col lg:flex-col-reverse"
              classNameName="max-w-lg flex-col-reverse"
            >
              <div tw="mb-10 lg:mt-10 flex justify-between items-center">
                {currentResume > 0 && (
                  <button onClick={handlePrevResume}>
                    <FontAwesomeIcon
                      icon={faArrowAltCircleLeft}
                      tw="text-xl mx-5"
                    />
                  </button>
                )}
                <button
                  tw="px-4 py-2 mr-5 bg-blue-800 text-gray-100 rounded text-lg"
                  disabled={!isValid}
                  onClick={handleDownload}
                >
                  <FontAwesomeIcon icon={faDownload} />
                  <span tw="inline-block ml-2">Download</span>
                </button>
                {currentResume < Resumes.length - 1 && (
                  <button onClick={handleNextResume}>
                    <FontAwesomeIcon
                      icon={faArrowCircleRight}
                      tw="text-xl mx-5"
                    />
                  </button>
                )}
              </div>
              <div
                tw="px-5 border-gray-200 border"
                style={{ height: "80vh", overflow: "scroll" }}
              >
                <DisplayedResume data={data} />
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

export default App;
